import * as React from "react";
import { Link } from "gatsby";
import * as styles from "../styles/index.module.css";

const formatPrice = (price) => {
    const match = price.match(/^(-?\d+(\.\d+)?)(.*)$/); // Regex to split numeric part and additional text
    if (!match) return price; // Return the original value if it doesn't match the pattern

    const numericPart = parseFloat(match[1]);
    const unit = match[3]?.trim(); // Capture any unit like '/hour'

    if (isNaN(numericPart)) return price; // Return original value if numeric part is not a number

    const formattedPrice = numericPart < 0 ? `-$${Math.abs(numericPart)}` : `$${numericPart}`;
    return (unit ? `${formattedPrice} ${unit}` : formattedPrice).replace(/\s+/g, ''); // Remove spaces
};

const TableRows = ({ data }) => {
    return data.map((item, index) => (
        <tr key={index}>
            <td>
                <div className={styles.pay_free}>
                    {item.paylink ? (
                        <Link to={item.paylink} target="_blank" rel="noopener noreferrer">
                            <button className={styles.priceButton}>
                                <strong>{formatPrice(item.price)}</strong>
                            </button>
                        </Link>
                    ) : (
                        <strong>{formatPrice(item.price)}</strong>
                    )}
                </div>
            </td>
            <td>
                <div className={styles.chart_padding}>
                    <strong>{item.description}</strong>
                    <br />
                    <span className={styles.small_text}>
                        <strong>{item.details}</strong>
                    </span>
                </div>
            </td>
        </tr>
    ));
};

export default TableRows;
