import React, { useState, useEffect, useRef, useLayoutEffect } from "react";
import { useSelector } from "react-redux";
import { navigate } from "gatsby";
import * as styles from "./headerLink.module.css";

/* Auto-sizing SVG button based on text width */
const AutoSizeSvgButton = ({ x, label, onClick }) => {
  const textRef = useRef(null);
  const [box, setBox] = useState(null);

  useLayoutEffect(() => {
    if (textRef.current) {
      const b = textRef.current.getBBox();
      setBox(b);
    }
  }, [label]);

  const paddingX = 30;
  const paddingY = 18;

  return (
    <g
      transform={`translate(${x}, 0)`}
      className={styles.svgButton}
      onClick={onClick}
      role="button"
      tabIndex={0}
      onKeyDown={(e) => {
        if (e.key === "Enter" || e.key === " ") {
          e.preventDefault();
          onClick();
        }
      }}
    >
      {box && (
        <rect
          x={box.x - paddingX}
          y={box.y - paddingY}
          width={box.width + paddingX * 2}
          height={box.height + paddingY * 2}
          rx="10"
          className={styles.svgButtonBackground}
        />
      )}

      <text
        ref={textRef}
        fill="white"
        fontFamily="Courier"
        fontSize="90px"
        dominantBaseline="middle"
        textAnchor="middle"
        pointerEvents="none"
      >
        {label}
      </text>
    </g>
  );
};

const ModularHeaderLink = ({
  leftWord = "walkum",
  leftLinkTo = "/",
  rightWord,
  rightLinkTo = ".",
  cursorColor = "#FFD700",
}) => {
  const [blinkSpeed, setBlinkSpeed] = useState(getRandomBlinkSpeed());
  const accountInfo = useSelector((state) => state.account.accountInfo);

  function getRandomBlinkSpeed() {
    return Math.random() * (3000 - 1000) + 1000;
  }

  useEffect(() => {
    const interval = setInterval(() => {
      setBlinkSpeed(getRandomBlinkSpeed());
    }, 5000);

    return () => clearInterval(interval);
  }, []);

  if (!accountInfo) return null;

  return (
    <div style={{ display: "flex", justifyContent: "center" }}>
      <div className={styles.phraseContainer}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          viewBox="0 0 1200 300"
          className={styles.responsiveSvg}
          preserveAspectRatio="xMidYMid meet"
        >
          <g transform="translate(600, 150)" textAnchor="middle">
            {/* LEFT BUTTON */}
            <AutoSizeSvgButton
              x={-300}
              label={leftWord}
              onClick={() => navigate(leftLinkTo)}
            />

            {/* CURSOR */}
            <text
              x="0"
              fill={cursorColor}
              fontFamily="Courier"
              fontSize="90px"
              fontWeight="bold"
              className={styles.blinkingCursor}
              style={{ animationDuration: `${blinkSpeed}ms` }}
              pointerEvents="none"
            >
              |
            </text>

            {/* RIGHT BUTTON */}
            <AutoSizeSvgButton
              x={300}
              label={rightWord}
              onClick={() => navigate(rightLinkTo)}
            />
          </g>
        </svg>
      </div>
    </div>
  );
};

export default ModularHeaderLink;
